* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 50px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

li {
  margin-bottom: 20px;
  margin-right: 10px;
}

a {
  color: #000;
  background-color: #d3d3d3;
  border: 2px double #000;
  border-radius: 10px;
  padding: 3px 8px;
  font-style: italic;
  font-weight: lighter;
  text-decoration: none;
  box-shadow: -5px -10px 10px gray;
}

a:hover, a:focus {
  color: #d3d3d3;
  background-color: #000;
  transform: translateX(.5px)rotate(-1deg)scale(.99);
}

button {
  color: #000;
  background-color: #d3d3d3;
  border-radius: 10px;
  margin-top: 30px;
  padding: 3px 8px;
  box-shadow: -5px -10px 10px gray;
}

button:not(:last-child) {
  margin-right: 20px;
}

input {
  background-color: #d3d3d3;
  border-radius: 10px;
  padding: 3px 8px;
}

